import 'angular';
import 'angular-resource';
import 'angular-sanitize';
import 'angular-strap';
import 'ui-select';

/**
 * 下拉框模块
 * @class NgModule.select
 * @alias gm.ngCustom.select
 * @requires ui.select
 * @author LP
 */
angular.module('gm.ngCustom.select', ['ui.select']).directive('gmDicSelect', [
    /**
     * @method gmDicSelect 数据字典指令
     * @member NgModule.select
     * @param {String} type DOM属性传值，数据字典类型
     * @param {String} binddata DOM属性传值，绑定的表达式
     * @param {String} disabled="false" 作用域传值，是否禁用
     * @param {String} gm-validator DOM属性传值，表单验证字符串
     */
    function() {
        return {
            restrict: 'ECA',
            template: require('./dic-select.tpl.html'),
            scope: {
                disabled: '=',
                binddata: '=',
                gmValidator: '='
            },
            compile: function compile(element, attr, transclude) {
                return {
                    pre: function preLink(scope, element, attr, controller) {
                        var bindName = attr['binddata'];
                        scope.bindName = bindName;
                        if (scope.gmValidator == null) {
                            if (attr['gmValidator']) {
                                scope.gmValidator = attr['gmValidator'];
                            }
                        }
                    },
                    post: function postLink(scope, element, attr, controller) {
                        var strType = attr['type'];
                        var bindName = attr['binddata'];
                        scope.validatorVal = '';
                        scope.dicElement = element;
                        scope.bindName = bindName;

                        //选择结果反馈到父作用域
                        scope.$watch('optionData.selected', function(newValue, oldValue, scope) {
                            if (newValue != null) {
                                scope.binddata = newValue.value;
                                scope.validatorVal = newValue.value;
                            } else {
                                scope.validatorVal = null;
                            }
                        });
                        //有默认值时默认选中
                        scope.$watch('binddata', function(newValue, oldValue, scope) {
                            if (newValue != null) {
                                setDefaultValue();
                            } else {
                                if (scope.optionData) {
                                    scope.optionData.selected = null;
                                }
                            }
                        });

                        //获取数据字典
                        if (GM.SysDicOper.dicData) {
                            //设置默认值
                            scope.optionData = angular.copy(GM.SysDicOper.dicData[strType]);
                            setDefaultValue();
                        }

                        //设置默认值
                        function setDefaultValue() {
                            var strValue = scope.binddata;
                            if (scope.optionData != null && strValue != null) {
                                for (var ii = 0; ii < scope.optionData.length; ii++) {
                                    if (String(scope.optionData[ii].value) === String(strValue)) {
                                        scope.optionData.selected = scope.optionData[ii];
                                        scope.validatorVal = scope.optionData.selected.value;
                                    }
                                }
                            }
                        }
                    }
                };
                // or
                // return function postLink( ... ) { ... }
            }
        };
    }
]).directive('gmDicSpan', [
    /**
     * @method gmDicSpan 将代码转换为标签值的指令
     * @member NgModule.select
     * @param {String} type DOM属性传值，数据字典类型
     * @param {String} binddata DOM属性传值，绑定的表达式
     */
    function() {
        return {
            restrict: 'ECA',
            template: '<span ng-bind="dicSpan"></span>',
            scope: true,
            link: function(scope, element, attr, transclution) {
                var strType = attr['type'];
                var binddata = attr['binddata'];
                scope.binddata = binddata;

                //有默认值时默认选中
                scope.$watch(binddata, function(newValue, oldValue, scope) {
                    if (newValue != null) {
                        setDefaultValue();
                    }
                });

                //获取数据字典
                if (GM.SysDicOper.dicData) {
                    //设置默认值
                    scope.optionData = GM.SysDicOper.dicData[strType];
                    setDefaultValue();
                }

                //设置默认值
                function setDefaultValue() {
                    var strValue = GM.CommonOper.getObjectValue(scope.$parent, scope.binddata);
                    if (scope.optionData != null && strValue != null) {
                        for (var ii = 0; ii < scope.optionData.length; ii++) {
                            if (String(scope.optionData[ii].value) === String(strValue)) {
                                scope.dicSpan = scope.optionData[ii].label;
                            }
                        }
                    }
                }
            }
        };
    }
]).directive('gmQuerySelect', [
    /**
     * @method gmQuerySelect 单选下拉搜索的指令
     * @member NgModule.select
     * @param {String} binddata 作用域传值，下拉框绑定的值
     * @param {Array} data 作用域传值，下拉框对应的数据集合
     * @param {Object} config 作用域传值，配置项
     * @param {Object} [config.attName='name'] 配置数据集合中显示内容的属性名
     * @param {Object} [config.strAttValue='value'] 配置数据集合中值的属性名
     * @param {Boolean} [config.pinyin=false] 配置是否开启拼音查询
     * @param {String} gm-validator 配置表单验证字符串
     * @param {Boolean} [disabled=false] 配置是否可用
     *
     */
    function() {
        return {
            restrict: 'ECA',
            template: require('./query-select.tpl.html'),
            scope: {
                binddata: '=',
                data: '=',
                config: '='
            },
            compile: function compile(element, attr, transclude) {
                return {
                    pre: function preLink(scope, element, attr, controller) {
                        var binddata = attr['binddata'];
                        scope.bindName = binddata;
                        if (scope.gmValidator == null) {
                            if (attr['gmValidator']) {
                                scope.gmValidator = attr['gmValidator'];
                            }
                        }
                    },
                    post: function postLink(scope, element, attr, controller) {
                        if (scope.config == null) {
                            scope.config = {};
                        }
                        scope.attName = scope.config.name ? scope.config.name : 'name';
                        scope.strAttValue = scope.config.attvalue ? scope.config.attvalue : 'value';
                        scope.toPinyin = scope.config.pinyin ? scope.config.pinyin : false;
                        var strDisabled = attr['disabled'];

                        //将数据传入
                        var result = angular.copy(scope.data);
                        result.forEach(function(value) {
                            value.label = value[scope.attName];
                            if (scope.toPinyin) {
                                value.pinyin = GM.ChineseConvertToPinyin.toPinyin(value[scope.attName]);
                            }
                        });
                        scope.optionData = result;
                        //判断是否有默认值
                        scope.$watch('binddata', function(newValue, oldValue, scope) {
                            if (newValue != null) {
                                setDefaultValue();
                            } else {
                                scope.optionData.selected = null;
                            }
                            scope.validatorVal = newValue;
                        });

                        //监听变化
                        scope.$watch('optionData.selected', function(newValue, oldValue, scope) {
                            if (scope.optionData.selected) {
                                scope.binddata = scope.optionData.selected[scope.strAttValue];
                            }
                        });

                        //是否可用
                        if (strDisabled === 'true') {
                            scope.bDisabled = true;
                        }

                        //设置默认值
                        function setDefaultValue() {
                            if (scope.optionData != null && scope.binddata != null) {
                                for (var ii = 0; ii < scope.optionData.length; ii++) {
                                    if (String(scope.optionData[ii][scope.strAttValue]) === String(scope.binddata)) {
                                        scope.optionData.selected = scope.optionData[ii];
                                    }
                                }
                            }
                        }
                    }
                };
            }
        };
    }
]);


