import 'angular';
import 'angular-resource';
import 'angular-strap';
import 'angular-ui-grid';
import '../../../../node_modules/angular-ui-grid/ui-grid.min.css';

/**
 * 表格模块
 * @class NgModule.table
 * @alias gm.ngCustom.table
 */
angular.module('gm.ngCustom.table', ['gm.ngCustom.utils', 'ngSanitize']).provider('$gmTable', function () {
    this.$get = [function () {
        /**
         * 表格服务类
         * @class NgModule.table.$gmTable
         * @alternateClassName $gmTable服务
         */
        function TableFactory (element, config) {
            var $gmTable = {};

            /**
             * 初始化表格
             * @member NgModule.table.$gmTable
             * @method init
             */
            $gmTable.init = function () {

            };

            /**
             * 从表格所有记录中获取选中记录
             * @member NgModule.table.$gmTable
             * @method getCheckData
             * @param {Array} arrayRecords 所有表格记录
             * @returns {Array} 选中记录集合
             */
            $gmTable.getCheckData = function (arrayRecords) {
                if (arrayRecords == null) {
                    return null;
                }
                var result = [];
                for (var ii = 0; ii < arrayRecords.length; ii++) {
                    var pRecord = arrayRecords[ii];
                    if (pRecord.chkItem === true) {
                        result.push(pRecord);
                    }
                }
                return result;
            };

            /**
             * 刷新表格大小
             * @member NgModule.table.$gmTable
             * @method updateSize
             */
            $gmTable.updateSize = function () {
                setTimeout(function () {
                    $(window).resize();
                }, 10);
            };

            /**
             * 获取表格操作列模板
             * @member NgModule.table.$gmTable
             * @method getCellTemplate
             * @param  {Array} operate 操作对象集合，集合元素为包含三个属性的对象
             *  click 点击操作项的函数名称
             *  icon  ZUI图标类
             *  name  操作显示值
             * @returns {string} 模板字符串
             */
            $gmTable.getCellTemplate = function (operate) {
                var cellTemplate = '<div class="ui-grid-cell-contents operate">';

                for (var ii = 0; ii < operate.length; ii++) {
                    var pItem = operate[ii];
                    var strAHtml = '<a href="javascript:void(0)" ng-click="grid.appScope.{0}(row,rowRenderIndex, colRenderIndex)"><i class="icon {1}"></i>{2}</a>';
                    strAHtml = GM.CommonOper.formatString(strAHtml, [pItem.click, pItem.icon, pItem.name]);
                    cellTemplate += strAHtml;
                }

                cellTemplate += '</div>';
                return cellTemplate;
            };

            /**
             * @member NgModule.table.$gmTable
             * @method getDicLabelTemplate 获取表格中数据字典的显示模板
             * @param strType {String} 数据字典类型
             * @returns {string} 单元格模板
             */
            $gmTable.getDicLabelTemplate = function (strType) {
                var strTemp = "{{grid.appScope.getDicLable(row.entity[col.name],'{0}')}}";
                strTemp = GM.CommonOper.formatString(strTemp, [strType]);
                var cellTemplate = '<div class="ui-grid-cell-contents" title="{0}">{0}</div>';
                cellTemplate = GM.CommonOper.formatString(cellTemplate, [strTemp]);
                return cellTemplate;
            };

            /**
             * @member NgModule.table.$gmTable
             * @method 格式化需要展示的数据
             * @param {Array} pTreeData 树节点数组
             * @param {String} strAttChildren 子节点属性名
             * @returns {Array} 结果数据
             */
            $gmTable.formatTreeNodes = function (pTreeData, strAttChildren) {
                var bRt = GM.TreeOper.setNodesLevel(pTreeData, strAttChildren, '$$treeLevel');
                if (bRt === false) {
                    return null;
                }

                //按照数组方式获取数据
                var result = GM.TreeOper.getAllNodes(pTreeData, strAttChildren);
                return result;
            };

            /**
             * @member NgModule.table.$gmTable
             * @method getIndentCellTemplate 获取树状表格第一列的单元格模板
             * @returns {string} 单元格模板
             */
            $gmTable.getIndentCellTemplate = function () {
                var strTemplate = '<div class="ui-grid-cell-contents" title="{{row.entity[col.name]}}" ng-style="grid.appScope.getStyleForTreeTable(row.entity.$$treeLevel*20)">{{row.entity[col.name]}}</div>';
                return strTemplate;
            };

            /**
             * @member NgModule.table.$gmTable
             * @method getCategoryData 获取表头分类数据
             * @param {Array} columnDefs 列配置集合
             * @returns {string} 单元格模板
             */
            $gmTable.getCategoryData = function (columnDefs) {
                if (columnDefs == null) {
                    return null;
                }
                var result = [];
                for (var ii = 0; ii < columnDefs.length; ii++) {
                    var pCol = columnDefs[ii];
                    var pCategory = null;
                    var pSubcategory = null;

                    //找一级表头
                    pCategory = this._getContainCategoryData(result, pCol.category);
                    if (pCol.category && pCategory == null) {
                        pCategory = {
                            name: pCol.category,
                            type: 'category'
                        };
                        result.push(pCategory);
                    }

                    //找二级表头
                    if (pCol.subcategory) {
                        pSubcategory = this._getContainCategoryData(result, pCol.subcategory, true);
                        if (pSubcategory == null) {
                            pSubcategory = {
                                name: pCol.subcategory,
                                type: 'subCategory'
                            };
                            if (pCategory.children == null) {
                                pCategory.children = [];
                            }
                            pCategory.children.push(pSubcategory);
                        }
                    }

                    //插入真实列
                    if (pSubcategory != null) {
                        if (pSubcategory.children == null) {
                            pSubcategory.children = [];
                        }
                        pSubcategory.children.push({
                            name: pCol.name,
                            type: 'column'
                        });
                    } else if (pCategory != null) {
                        if (pCategory.children == null) {
                            pCategory.children = [];
                        }
                        pCategory.children.push({
                            name: pCol.name,
                            type: 'column'
                        });
                    } else {
                        result.push({
                            name: pCol.name,
                            type: 'column'
                        });
                    }
                }

                return result;
            };

            /**
             * @member NgModule.table.$gmTable
             * @method _getContainCategoryData 获取表头分类数据
             * @param {Array} arrayData 分类数据集合
             * @param {String} strName 名称
             * @param {Boolean} [bSubCategory=false] 是否为子集分类
             * @ignore
             * @private
             * @returns {string} 单元格模板
             */
            $gmTable._getContainCategoryData = function (arrayData, strName, bSubCategory) {
                if (arrayData == null) {
                    return null;
                }
                for (var ii = 0; ii < arrayData.length; ii++) {
                    var pItem = arrayData[ii];
                    if (bSubCategory !== true) {
                        if (pItem.name === strName && pItem.type === 'category') {
                            return pItem;
                        }
                    } else {
                        if (pItem.children) {
                            for (var jj = 0; jj < pItem.children.length; jj++) {
                                var pSubItem = pItem.children[jj];
                                if (pSubItem.name === strName && pSubItem.type === 'subCategory') {
                                    return pSubItem;
                                }
                            }
                        }
                    }
                }
                return null;
            };

            return $gmTable;
        }
        return TableFactory;
    }];
}).directive('gmTable', ['$gmTable',
    /**
     * 表格指令
     * @member NgModule.table
     * @method gmTable
     * @param {Boolean} checkable 是否可选，DOM参数
     * @param {Object} tabledata 作用域传进来的参数
     */
    function ($gmTable) {
        var $pGmTable = new $gmTable();
        return {
            restrict: 'EA',
            template: require('./table.tpl.html'),
            scope: {
                tabledata: '='
            },
            link: function postLink (scope, element, attr, transclution) {
                //设置配置信息
                var bCheck = attr['checkable'];
                if (bCheck === 'true') {
                    scope.checkable = true;
                }

                if (scope.tabledata && scope.tabledata.config) {
                    for (var key in scope.tabledata.config) {
                        var itemConfig = scope.tabledata.config[key];
                        if (key === 'operate') {
                            for (var ii = 0; ii < itemConfig.length; ii++) {
                                var subItem = itemConfig[ii];
                                scope[subItem.click] = scope.$parent[subItem.click];
                            }
                        } else {
                            scope[itemConfig.click] = scope.$parent[itemConfig.click];
                        }
                    }
                }

                //获取数据字典对应的label值
                scope.getLabel = function (strType, strValue) {
                    var result = GM.SysDicOper.getLabel(strType, strValue);
                    return result;
                };

                //注册事件
                //注册全选事件
                scope.all = function (checks) {
                    var arrayRecords = scope.tabledata.data.page.records;
                    for (var ii = 0; ii < arrayRecords.length; ii++) {
                        arrayRecords[ii].chkItem = checks;
                    }
                    var arrayCheckedData = $pGmTable.getCheckData(scope.tabledata.data.page.records);
                    if (angular.isFunction(scope.$parent[scope.tabledata.config.checkCallback])) {
                        scope.$parent[scope.tabledata.config.checkCallback](arrayCheckedData);
                    }
                };

                //注册
                scope.checkItem = function (chkItem) {
                    var arrayCheckedData = $pGmTable.getCheckData(scope.tabledata.data.page.records);
                    if (angular.isFunction(scope.$parent[scope.tabledata.config.checkCallback])) {
                        scope.$parent[scope.tabledata.config.checkCallback](arrayCheckedData);
                    }
                    var arrayRecords = scope.tabledata.data.page.records;
                    //有一个为false则全选按钮为不选中
                    if (!chkItem) {
                        scope.checks = false;
                    } else {
                        for (var i = 0; i < arrayRecords.length; i++) {
                            //只要有一个按钮没有选中
                            var pRecord = arrayRecords[i];
                            if (!pRecord.chkItem) {
                                return;
                            }
                        }
                        //全部选中
                        scope.checks = true;
                    }
                };
                scope.$watch('tabledata.data.page.records', function (newValue, oldValue, scope) {
                    scope.checks = false;
                });
            }
        };
    }
]).filter('to_trusted', ['$sce', function ($sce) {
    return function (text) {
        return $sce.trustAsHtml(text);
    };
}]);



