angular.module('gm.ngCustom.control', ['mgcrea.ngStrap', 'gm.ngCustom.table', 'gm.ngCustom.tree', 'gm.ngCustom.pagination',
    'gm.ngCustom.select', 'gm.ngCustom.houseTable', 'gm.ngCustom.statistics', 'gm.ngCustom.utils', 'ui.tree', 'ui.calendar',
    'gm.ngCustom.tabsWrapper', 'gm.ngCustom.layout', 'ui.grid', 'ui.grid.cellNav', 'angular-carousel', 'ui.select',
    'ui.grid.edit', 'ui.grid.resizeColumns', 'ui.grid.pinning', 'ui.grid.selection', 'ui.grid.moveColumns', 'cfp.loadingBarInterceptor',
    'ui.grid.exporter', 'ui.grid.importer', 'ui.grid.grouping', 'ui.grid.autoResize', 'ui.grid.treeView', 'bm.uiTour',
    'ngDialog', 'validation.rule', 'me-pageloading', 'cgBusy', 'froala'
]);

angular.module('gm.ngCustom.table').run(['$templateCache', function ($templateCache) {
    let strTemp = require('./table/custom-header.html');
    $templateCache.put('table-header.tpl.html', strTemp);
}]);
angular.module('bm.uiTour').run(['$templateCache', function ($templateCache) {
    let strTemp = require('./tour/tour-template.html');
    $templateCache.put('tour-step-template.html', strTemp);
}]);



