import 'angular-strap';
import 'angular-resource';
import 'angular-sanitize';
import 'datetimepicker';
import 'datetimepickerCN';

/**
 * 页面通用小控件模块
 * @class NgModule.layout
 * @alias gm.ngCustom.layout
 * @author LP
 */
angular.module('gm.ngCustom.layout', []).provider('$layoutTag',
    /**
     * 表示查询项的标签的服务
     * @class NgModule.layout.$layoutTag
     * @alias $layoutTag
     * @author LP
     */
    function () {
        this.$get = [function () {
            function factory () {
                var $layout = {};

                /**
                 * @member NgModule.layout.$layoutTag
                 * @method  getCheckedTags 获取选中的标签
                 * @param {Array} tags 标签集合
                 * @returns {Array} 选中的标签集合
                 */
                $layout.getCheckedTags = function (tags) {
                    if (tags == null) {
                        return null;
                    }
                    var result = [];
                    for (var ii = 0; ii < tags.length; ii++) {
                        if (tags[ii].checked) {
                            result.push(tags[ii]);
                        }
                    }
                    return result;
                };

                /**
                 * @member NgModule.layout.$layoutTag
                 * @method clearCheck 清空选择
                 * @param {Array} tags 标签集合
                 */
                $layout.clearCheck = function (tags) {
                    if (tags == null) {
                        return;
                    }
                    for (var ii = 0; ii < tags.length; ii++) {
                        tags[ii].checked = false;
                    }
                };
                return $layout;
            }
            return factory;
        }];
    }).directive('gmTags', ['$layoutTag',
    /**
     * @member NgModule.layout
     * @method gmTags 标签指令，EAC模式
     * @param {Number} [max-tag-num] DOM属性传值，外部显示出来的标签最大个数，默认会自动根据页面宽度计算
     * @param {Boolean} [multi = true] DOM属性传值，是否开启多选模式
     * @param {Object} tagdata 作用域传值，指令配置项
     * @param {String} tagdata.checkTag 选中标签后调用函数的名称
     * @param {Array} tagdata.tags 标签数据
     */
    function ($layoutTag) {
        //查询表单中标签指令
        return {
            restrict: 'ECA',
            template: require('./tag.tpl.html'),
            scope: {
                tagdata: '='
            },
            link: function (scope, element, attr, transclution) {
                var pLayoutTag = new $layoutTag();
                var nMaxTagNum = Number(attr['maxTagNum']);
                var bMulti = attr['multi'] !== 'false'; //默认多选
                scope.bMulti = bMulti;

                //element.hide();
                if (Number.isNaN(nMaxTagNum) || nMaxTagNum === 0) {
                    //计算可以存放的最大个数
                    GM.CommonOper.delayExcute(function (params) {
                        var arrayItems = params.element.find('.tag-item');
                        if (arrayItems.length === 0 || $(arrayItems[0]).width() <= 0 || params.element.width() <= 0) {
                            return false;
                        } else {
                            //减去更多选择的按钮宽度
                            var nRemainWidth = params.element.width() - 50;
                            //多选状态需要减去 不限 按钮的宽度
                            if (bMulti === true) {
                                nRemainWidth -= 52;
                            }
                            for (var ii = 0; ii < arrayItems.length; ii++) {
                                var $pItem = $(arrayItems[ii]);
                                nRemainWidth -= $pItem.outerWidth() + 10;
                                if (nRemainWidth <= 0) {
                                    params.scope.maxTagNum = ii;
                                    params.scope.$apply('maxTagNum');
                                    break;
                                }
                            }
                            //params.element.show();
                            return true;
                        }
                    }, {
                        scope: scope,
                        element: element
                    });
                } else {
                    scope.maxTagNum = nMaxTagNum;
                }

                //查看额外的标签
                scope.clickExtraTool = function () {
                    scope.extraDown = !scope.extraDown;
                };

                //选中tag标签
                scope.checkTag = function (item) {
                    if (bMulti) {
                        //多选
                        item.checked = !item.checked;
                        if (angular.isFunction(scope.$parent[scope.tagdata.checkTag])) {
                            var arrayCheckedNodes = pLayoutTag.getCheckedTags(scope.tagdata.tags);
                            if (GM.CommonOper.isFunction(scope.$parent[scope.tagdata.checkTag])) {
                                scope.$parent[scope.tagdata.checkTag](arrayCheckedNodes);
                            }
                        }
                    } else {
                        //单选
                        if (item.checked === true) {
                            //已经选中,再次点击，则取消选中
                            item.checked = false;
                            if (GM.CommonOper.isFunction(scope.$parent[scope.tagdata.checkTag])) {
                                scope.$parent[scope.tagdata.checkTag](null);
                            }
                        } else {
                            pLayoutTag.clearCheck(scope.tagdata.tags);
                            item.checked = true;
                            if (GM.CommonOper.isFunction(scope.$parent[scope.tagdata.checkTag])) {
                                scope.$parent[scope.tagdata.checkTag](item);
                            }
                        }
                    }
                };

                //清空选择
                scope.clearCheck = function () {
                    pLayoutTag.clearCheck(scope.tagdata.tags);
                    if (GM.CommonOper.isFunction(scope.$parent[scope.tagdata.checkTag])) {
                        scope.$parent[scope.tagdata.checkTag]([]);
                    }
                };

                //点击其他位置，页面消失
                $(document).click(function () {
                    scope.extraDown = false;
                    scope.$apply('extraDown');
                });
                element.click(function (e) {
                    e.stopPropagation();
                    return false;
                });
            }
        };
    }
]).directive('dateChoose',
    /**
     * @member NgModule.layout
     * @param {Boolean} [btime=false] DOM属性传值，是否可选择时间
     * @param {Number} [minute-step=5] DOM属性传值，可选的时间间隔，有小时视图时，分钟间隔数
     * @param {Number} [max-view=2] DOM属性传值，最大视图值，取如下值
     *  0 小时视图
     *  1 日视图
     *  2 月视图
     *  3 年视图
     *  4 10年视图
     * @param {Number} [min-view=2] DOM属性传值，最小视图值，取值见 max-view
     * @param {Number} [start-view=max-view] DOM属性传值，初始视图值，取值见 max-view
     * @method dateChoose 日期指令，EAC模式
     */
    function () {
        return {
            restrict: 'EAC',
            compile: function compile (element, attr, transclude) {
                return {
                    pre: function preLink (scope, element, attr, controller) {
                        //日期选择器初始化函数
                        var pConfig = {
                            weekStart: 1,
                            todayBtn: 1,
                            autoclose: 1,
                            todayHighlight: 1,
                            forceParse: 0,
                            bootcssVer: 2,
                            language: 'zh-CN'
                        };
                        if (attr['btime'] === 'true') {
                            //开始时间选择模式
                            pConfig.format = 'yyyy-mm-dd hh:ii';
                            pConfig.minView = 0;
                            pConfig.maxView = 2;
                            pConfig.startView = 2;
                            pConfig.minuteStep = attr['minuteStep'] == null ? 5 : Number(attr['minuteStep']);
                        } else {
                            pConfig.minView = Number.isNaN(Number(attr['minView'])) ? 2 : Number(attr['minView']);
                            pConfig.maxView = Number.isNaN(Number(attr['maxView'])) ? 2 : Number(attr['maxView']);
                            pConfig.startView = Number.isNaN(Number(attr['startView'])) ? pConfig.maxView : Number(attr['startView']);
                            pConfig.minuteStep = attr['minuteStep'] == null ? 5 : Number(attr['minuteStep']);

                            //获取时间格式
                            if (pConfig.minView === 4) {
                                pConfig.format = 'yyyy';
                            } else if (pConfig.minView === 3) {
                                pConfig.format = 'yyyy-mm';
                            } else if (pConfig.minView === 2) {
                                pConfig.format = 'yyyy-mm-dd';
                            } else if (pConfig.minView === 1) {
                                pConfig.format = 'yyyy-mm-dd hh';
                            } else {
                                pConfig.format = 'yyyy-mm-dd hh:ii';
                            }
                        }
                        $(element).datetimepicker(pConfig);
                        $(element).wrap('<div class="date-choose-div"></div>');
                        $(element).after($('<i class="icon icon-calendar"></i>'));
                    },
                    post: function postLink (scope, element, attr, controller) {

                    }
                };
            }
        };
    }).directive('timeChoose',
    /**
     * @member NgModule.layout
     * @param {Number} [minute-step=5] DOM属性传值，可选的时间间隔
     * @method timeChoose 时间指令，EAC模式
     */
    function () {
        return {
            restrict: 'EAC',
            compile: function compile (element, attr, transclude) {
                return {
                    pre: function preLink (scope, element, attr, controller) {
                        var minuteStep = attr['minuteStep'] == null ? 5 : Number(attr['minuteStep']);

                        $(element).datetimepicker({
                            weekStart: 1,
                            todayBtn: 1,
                            autoclose: 1,
                            todayHighlight: 1,
                            minuteStep: minuteStep,
                            startView: 1,
                            minView: 0,
                            maxView: 1,
                            forceParse: 0,
                            format: 'hh:ii',
                            bootcssVer: 2,
                            language: 'zh-CN'
                        });
                        $(element).wrap('<div class="date-choose-div"></div>');
                        $(element).before($('<i class="icon icon-calendar"></i>'));
                    },
                    post: function postLink (scope, element, attr, controller) {

                    }
                };
            }
        };
    }).directive('carouselNotify', ['$interval',
    /**
     * @member NgModule.layout
     * @method carouselNotify 轮播通知指令，EAC模式
     */
    function ($interval) {
        return {
            restrict: 'EAC',
            template: require('./carousel-notify.tpl.html'),
            scope: {
                notifyData: '='
            },
            link: function (scope, element, attr, transclution) {
                var strInterval = attr['interval'];
                strInterval = strInterval == null ? '1000' : strInterval;
                var nInterval = Number(strInterval);
                scope.animationStyle = {
                    'transition': '0.5s linear all'
                };
                scope.spanStyle = {};
                scope.nSwapIndex = 0;
                scope.left = 0;

                $interval(function () {
                    var nDivWidth = $(element).find('.swap-animation').width();
                    var nSpanWidth = $(element).find('.swap-animation span').outerWidth();
                    if (scope.left + nDivWidth < nSpanWidth) {
                        //先横向滚动
                        scope.left += nDivWidth - 10;
                    } else {
                        //横向滚动结束，开始竖向滚动
                        if (scope.notifyData != null && scope.notifyData.length > 0) {
                            scope.nSwapIndex++;
                            var nIndex = scope.nSwapIndex % scope.notifyData.length;
                            scope.message = scope.notifyData[nIndex];
                        }
                        scope.left = 0;
                    }
                    scope.spanStyle.left = '-' + scope.left + 'px';
                }, nInterval);

                if (scope.notifyData) {
                    scope.message = scope.notifyData[scope.nSwapIndex];
                }
            }
        };
    }
]);



