import 'base';
import 'jquery';
import * as sweetalert from 'sweetalert';

swal = sweetalert.default;

/**
 * @description 工具提示控件
 * @class GM.Tooltip
 * @ignore
 * @depercated
 */
GM.Tooltip = {
    /**
     * @description 默认设置
     */
    options: {
        placement: 'right',
        trigger: 'focus'
    },
    /**
     * @description 展示控件
     * @method show
     * @param {Object} $Ele jquery对象
     * @param {String} strMessage 要展示的信息
     * @param {Object} options 配置选项
     */
    show: function($Ele, strMessage, options) {
        if ($Ele && $Ele.tooltip) {
            this.options = GM.Util.setOptions(this, options);
            this.options.title = strMessage;
            $Ele.tooltip(this.options).tooltip('show');
        }
    },
    /**
     * @description 关闭控件
     * @method close
     * @param {Object} $Ele jquery对象
     */
    close: function($Ele) {
        if ($Ele && $Ele.tooltip) {
            $Ele.tooltip('destroy');
        }
        $('.tooltip.fade.right').remove();
    }
};

/**
 * @description 带箭头的弹出框
 * @class GM.Popover
 * @ignore
 * @depercated
 */
GM.Popover = {
    /**
     * @description 默认设置
     */
    options: {
        placement: 'bottom',
        trigger: 'focus',
        html: true
    },
    show: function($Ele, strTitle, strContent, options) {
        if ($Ele && $Ele.tooltip) {
            this.options = GM.Util.setOptions(this, options);
            this.options.title = strTitle;
            this.options.content = strContent;
            $Ele.popover(this.options).popover('show');
        }
    },
    toggle: function($Ele, strTitle, strContent, options) {
        if ($Ele && $Ele.tooltip) {
            this.options = GM.Util.setOptions(this, options);
            this.options.title = strTitle;
            this.options.content = strContent;
            this.options.trigger = 'manual';
            $Ele.popover(this.options).popover('toggle');
        }
    },
    close: function($Ele) {
        if ($Ele && $Ele.tooltip) {
            $Ele.popover('destroy');
        }
    }
};

/**
 * @description 美化Alert弹出框
 */
GM.Alert = {
    /**
     * 默认设置
     * @property {Object} options
     * @property {String} options.title="标题" 标题
     * @property {String} options.text="完成" 提示内容
     * @property {"warning"/"error"/"success"/"info"} options.type="info" 弹框类型
     * @property {Boolean} options.allowOutsideClick=false 是否允许点击外部之后弹框消失
     * @property {Boolean} options.showCancelButton=false 是否展示取消按钮
     * @property {String} options.confirmButtonText="取消" 确认按钮上显示的文字
     * @property {String} options.confirmButtonColor="#AEDEF4" 确认按钮颜色
     * @property {String} options.cancelButtonText="取消" 取消按钮上显示的文字
     * @property {String} options.imageUrl=null 图片路径
     * @property {String} options.imageSize="80x80" 图片大小
     */
    options: {
        title: '标题',
        text: '完成',
        type: 'info',
        allowOutsideClick: false,
        showCancelButton: false,
        confirmButtonText: '确认',
        confirmButtonColor: '#AEDEF4',
        cancelButtonText: '取消',
        imageUrl: null,
        imageSize: '80x80'
    },
    /**
     * 展示弹出框
     *
     *```
     *GM.Alert.show("提示","是否确认删除？",{
     *      type:"warning"
     *});
     *```
     *
     * @static
     * @method show
     * @param {String} strTitle 弹出框标题名称
     * @param {String} strContent 弹出框展示内容
     * @param {Object} options 配置选项
     * @param {Function} callback 确认时调用的函数
     *
     */
    show: function(strTitle, strContent, options, callback) {
        if (swal) {
            this.options = GM.Util.setOptions(this, options);
            this.options.title = strTitle;
            this.options.text = strContent;
            try {
                swal(this.options).then(() => {
                    callback();
                });
            } catch (ex) {
                alert(strContent);
            }
        }
    }
};

/**
 * @description 消息框
 * @class GM.Infomation
 * @author LP
 * @static
 */
GM.Infomation = {
    /**
     * 默认设置
     * @property {Object} options
     * @property {'primary'|'success'|'info'|'warning'|'danger'|'important'|'special'} options.type="default" 弹出框类型
     * @property {'bottom'|'top-left'|'top-right'|'bottom-left'|'bottom-right'|'center'} options.placement="完成" 弹出框弹出位置
     * @property {Number} options.time="4000 弹框自动消失时间，单位毫秒
     * @property {String} options.parent="body" 父容器选择器
     * @property {String} options.icon="info-sign" 弹框图标，可以选择zui中图标样式也可以使用
     *                                              {@link GM.Infomation#getIcoFromType getIcoFromType}函数获取
     * @property {Boolean} options.close=true 是否有关闭按钮
     * @property {Boolean} options.fade=true 是否有渐入渐出的动画效果
     * @property {Boolean} options.scale=true 是否有放大缩小动画效果
     */
    options: {
        /**
         * @description 弹出框类型，有以下几种'primary'|'success'|'info'|'warning'|'danger'|'important'|'special'
         */
        type: 'default',
        /**
         * @description 弹出框位置，有以下几种 'bottom'|'top-left'|'top-right'|'bottom-left'|'bottom-right'|'center'
         */
        placement: 'top',
        /**
         * @description 自动消失事件，单位ms
         */
        time: 4000,
        /**
         * @description 父窗口
         */
        parent: 'body',
        /**
         * @description 消息框前面的图标
         */
        icon: 'info-sign',
        /**
         * @description 是否有关闭按钮
         */
        close: true,
        /**
         * @description 如果为true，则在显示和隐藏消息时使用渐隐渐显的动画效果。
         */
        fade: true,
        /**
         * @description 如果为true，则在显示和隐藏消息时使用缩放的动画效果。
         */
        scale: true
    },
    /**
     * 展示非阻挡式消息框，阻挡式弹出框见{@link GM.Alert#show GM.Alert}
     * @static
     * @method show
     * @param {String} strMessage 弹出框内容
     * @param options {GM.Infomation.options}
     *
     */
    show: function(strMessage, options) {
        this.options = GM.Util.setOptions(this, options);
        this.getIcoFromType();
        this.msg = new $.zui.Messager(strMessage, this.options);
        this.msg.show();
    },
    close: function() {
        this.msg.hide();
    },
    /**
     * @description 根据不同类型的消息框设置相应的图标字符串
     * @method getIcoFromType
     */
    getIcoFromType: function() {
        switch (this.options.type) {
            case 'default':
            case 'primary':
                this.options.icon = 'bell-alt';
                break;
            case 'success':
                this.options.icon = 'ok-sign';
                break;
            case 'info':
                this.options.icon = 'info-sign';
                break;
            case 'warning':
                this.options.icon = 'warning-sign';
                break;
            case 'danger':
                this.options.icon = 'exclamation-sign';
                break;
            case 'important':
                this.options.icon = 'star';
                break;
            case 'special':
                this.options.icon = 'heart';
                break;
        }
    }
};

/**
 * @description 全屏模态框（遮罩层）
 * @depercated
 * @ignore
 */
GM.FullModal = {
    /**
     * @description 展示对话框
     * @method show
     * @param {String} strTitle 标题
     * @param {String} strUrl 路径
     */
    show: function(strTitle, strUrl) {
        top.$.zui.modalTrigger.show({
            iframe: strUrl,
            size: 'fullscreen',
            title: strTitle,
            position: 'center',
            backdrop: 'static',
            moveable: true
        });
    },
    /**
     * @description 展示自定义对话框
     * @method showCustomModal
     * @param {String} strTitle 标题
     * @param {String} strUrl 路径
     * @param {Object} options 配置参数
     */
    showCustomModal: function(strTitle, strUrl, options) {
        options['iframe'] = strUrl;
        options['title'] = strTitle;
        top.$.zui.modalTrigger.show(options);
    }
};


